"use client"

import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Table } from "@/components/ui/table"
import { useState } from "react"
import { useUsers } from "../hooks/useUsers"

interface User {
  id: number;
  name: string;
  email: string;
  phone: string;
  student_id: string;
  status: string;
  address?: string;
}

export default function Users() {
  const { users, loading, error, addUser, updateUser, deleteUser } = useUsers()
  const [showNewUserForm, setShowNewUserForm] = useState(false)
  const [showEditForm, setShowEditForm] = useState(false)
  const [selectedUser, setSelectedUser] = useState<User | null>(null)
  
  // Add state for form inputs
  const [newUser, setNewUser] = useState({
    name: "",
    email: "",
    phone: "",
    student_id: "",
    address: ""
  })

  const handleNewUser = () => {
    // Set default student ID when opening the form
    setNewUser({
      ...newUser,
      student_id: `STU${String(users.length + 1).padStart(3, '0')}`
    })
    setShowNewUserForm(true)
  }

  const handleEdit = (user: User) => {
    setSelectedUser(user)
    setShowEditForm(true)
  }

  const handleDelete = async (userId: number) => {
    if (confirm("Are you sure you want to delete this user?")) {
      try {
        await deleteUser(userId)
      } catch (error) {
        alert("Failed to delete user")
      }
    }
  }

  const handleAddUser = async () => {
    try {
      await addUser({ ...newUser, status: "Active" })
      setNewUser({ name: "", email: "", phone: "", student_id: "", address: "" })
      setShowNewUserForm(false)
    } catch (error) {
      alert("Failed to add user")
    }
  }

  const handleUpdateUser = async () => {
    if (!selectedUser) return

    try {
      await updateUser(selectedUser.id, selectedUser)
      setShowEditForm(false)
      setSelectedUser(null)
    } catch (error) {
      alert("Failed to update user")
    }
  }

  if (loading) {
    return (
      <div className="container mx-auto p-4">
        <div className="text-center">Loading...</div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="container mx-auto p-4">
        <div className="text-center text-red-600">Error: {error}</div>
      </div>
    )
  }

  return (
    <div className="container mx-auto">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-4xl font-bold text-red-950">User Management</h1>
        <Button 
          className="bg-red-950 hover:bg-red-900 text-white"
          onClick={handleNewUser}
        >
          Add New User
        </Button>
      </div>

      {/* Quick Stats */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        <Card className="p-6 bg-red-50 border-red-100">
          <h3 className="text-lg font-semibold text-red-900">Total Users</h3>
          <p className="text-3xl font-bold text-red-950">{users.length}</p>
          <p className="text-sm text-red-700 mt-2">Registered members</p>
        </Card>
        
        <Card className="p-6 bg-red-50 border-red-100">
          <h3 className="text-lg font-semibold text-red-900">Active Users</h3>
          <p className="text-3xl font-bold text-red-950">
            {users.filter(user => user.status === "Active").length}
          </p>
          <p className="text-sm text-red-700 mt-2">Currently active</p>
        </Card>
        
        <Card className="p-6 bg-red-50 border-red-100">
          <h3 className="text-lg font-semibold text-red-900">New This Month</h3>
          <p className="text-3xl font-bold text-red-950">
            {users.length > 0 ? users.length : 0}
          </p>
          <p className="text-sm text-red-700 mt-2">Recent registrations</p>
        </Card>
      </div>

      {/* Search and Filter */}
      <Card className="p-6 mb-8 border-red-100">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div>
            <label className="block text-sm font-medium text-red-900 mb-2">
              Search Users
            </label>
            <Input 
              type="text" 
              placeholder="Search by name, email, or ID"
              className="border-red-200 focus:border-red-500 focus:ring-red-500"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-red-900 mb-2">
              Status
            </label>
            <select className="w-full border-red-200 rounded-md focus:border-red-500 focus:ring-red-500">
              <option value="">All Status</option>
              <option value="active">Active</option>
              <option value="inactive">Inactive</option>
              <option value="suspended">Suspended</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-red-900 mb-2">
              Join Date
            </label>
            <Input 
              type="date" 
              className="border-red-200 focus:border-red-500 focus:ring-red-500"
            />
          </div>
        </div>
      </Card>

      {/* Users Table */}
      <Card className="border-red-100">
        <div className="overflow-x-auto">
          <Table>
            <thead>
              <tr className="bg-red-50">
                <th className="text-left p-4 text-red-900">Name</th>
                <th className="text-left p-4 text-red-900">Email</th>
                <th className="text-left p-4 text-red-900">Phone</th>
                <th className="text-left p-4 text-red-900">Student ID</th>
                <th className="text-left p-4 text-red-900">Status</th>
                <th className="text-left p-4 text-red-900">Actions</th>
              </tr>
            </thead>
            <tbody>
              {users.map((user) => (
                <tr key={user.id} className="border-b border-red-100">
                  <td className="p-4">{user.name}</td>
                  <td className="p-4">{user.email}</td>
                  <td className="p-4">{user.phone}</td>
                  <td className="p-4">{user.student_id}</td>
                  <td className="p-4">
                    <span className="px-2 py-1 bg-green-100 text-green-800 rounded-full text-sm">
                      {user.status}
                    </span>
                  </td>
                  <td className="p-4">
                    <div className="flex gap-2">
                      <Button 
                        variant="outline" 
                        className="text-red-950 border-red-200 hover:bg-red-50"
                        onClick={() => handleEdit(user)}
                      >
                        Edit
                      </Button>
                      <Button 
                        variant="outline" 
                        className="text-red-950 border-red-200 hover:bg-red-50"
                        onClick={() => handleDelete(user.id)}
                      >
                        Delete
                      </Button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </div>
      </Card>

      {/* New User Form Modal */}
      {showNewUserForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4">
          <Card className="w-full max-w-md p-6 bg-white">
            <h2 className="text-2xl font-bold text-red-950 mb-4">Add New User</h2>
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Full Name
                </label>
                <Input 
                  type="text" 
                  value={newUser.name}
                  onChange={(e) => setNewUser({...newUser, name: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Email
                </label>
                <Input 
                  type="email" 
                  value={newUser.email}
                  onChange={(e) => setNewUser({...newUser, email: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Phone Number
                </label>
                <Input 
                  type="tel" 
                  value={newUser.phone}
                  onChange={(e) => setNewUser({...newUser, phone: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Student ID
                </label>
                <Input 
                  type="text" 
                  value={newUser.student_id}
                  onChange={(e) => setNewUser({...newUser, student_id: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Address
                </label>
                <textarea 
                  value={newUser.address}
                  onChange={(e) => setNewUser({...newUser, address: e.target.value})}
                  className="w-full border-red-200 rounded-md focus:border-red-500 focus:ring-red-500"
                  rows={3}
                />
              </div>
              <div className="flex gap-2 justify-end mt-6">
                <Button 
                  variant="outline" 
                  className="text-red-950 border-red-200 hover:bg-red-50"
                  onClick={() => setShowNewUserForm(false)}
                >
                  Cancel
                </Button>
                <Button 
                  className="bg-red-950 hover:bg-red-900 text-white"
                  onClick={handleAddUser}
                >
                  Add User
                </Button>
              </div>
            </div>
          </Card>
        </div>
      )}

      {/* Edit User Form Modal */}
      {showEditForm && selectedUser && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4">
          <Card className="w-full max-w-md p-6 bg-white">
            <h2 className="text-2xl font-bold text-red-950 mb-4">Edit User</h2>
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Full Name
                </label>
                <Input 
                  type="text" 
                  value={selectedUser.name}
                  onChange={(e) => setSelectedUser({...selectedUser, name: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Email
                </label>
                <Input 
                  type="email" 
                  value={selectedUser.email}
                  onChange={(e) => setSelectedUser({...selectedUser, email: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Phone Number
                </label>
                <Input 
                  type="tel" 
                  value={selectedUser.phone}
                  onChange={(e) => setSelectedUser({...selectedUser, phone: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Student ID
                </label>
                <Input 
                  type="text" 
                  value={selectedUser.student_id}
                  onChange={(e) => setSelectedUser({...selectedUser, student_id: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Status
                </label>
                <select 
                  value={selectedUser.status}
                  onChange={(e) => setSelectedUser({...selectedUser, status: e.target.value})}
                  className="w-full border-red-200 rounded-md focus:border-red-500 focus:ring-red-500"
                >
                  <option value="Active">Active</option>
                  <option value="Inactive">Inactive</option>
                  <option value="Suspended">Suspended</option>
                </select>
              </div>
              <div className="flex gap-2 justify-end mt-6">
                <Button 
                  variant="outline" 
                  className="text-red-950 border-red-200 hover:bg-red-50"
                  onClick={() => {
                    setShowEditForm(false)
                    setSelectedUser(null)
                  }}
                >
                  Cancel
                </Button>
                <Button 
                  className="bg-red-950 hover:bg-red-900 text-white"
                  onClick={handleUpdateUser}
                >
                  Save Changes
                </Button>
              </div>
            </div>
          </Card>
        </div>
      )}
    </div>
  )
}
